body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #ffffff;
}

.container {
  max-width: 900px;
  margin: auto;
  padding: 40px;
}

textarea {
  width: 100%;
  background-color: #1e1e1e;
  color: white;
  border: 1px solid #333;
  padding: 15px;
  border-radius: 8px;
  font-size: 14px;
}

button {
  background-color: #00adb5;
  color: black;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  margin-top: 10px;
}

button:hover {
  background-color: #00f5ff;
}

.section {
  margin-top: 30px;
}

ul {
  padding-left: 20px;
}
.loader {
  margin-top: 20px;
  border: 5px solid #1e1e1e;
  border-top: 5px solid #00adb5;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
